.popup__reset {
  position: absolute;
  top: -40px;
  right: -40px;
  width: 40px;
  height: 40px;
  border: none;
  background: transparent url('../../../images/popup/CloseIcon.svg') no-repeat center top;
}

.popup__reset:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 515px) {
  .popup__reset {
    top: -55px;
    right: calc(50% - 18px);
    background-image: url('../../../images/popup/CloseIconSm.svg');
  }
}
